import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home'
// import Home from '@/components/editor'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    }
  ]
})



